	loadCover(t){
		var image = new Image();
		var canvas=this.state.logoimg

		image.src = canvas.toDataURL("image/jpeg");
		image.crossOrigin = "anonymous";
		console.log(image.src)

		var url = t.logo.url+"?type="+t.logo.params.type+"&eid="+t.logo.params.eid+"&token="+this.state.token; // File upload web service path
		var photo = {
			uri: image.src, // CameralRoll Url
			type: 'image/jpeg',
			name: 'photo.jpg',
		};

		var formData = new FormData();
		formData.append("file", photo);

		var xhr = new XMLHttpRequest();
		xhr.open('POST', url);

		xhr.onprogress = function () {
		};

		xhr.onload = function () {
		};

		xhr.send(image.src);
	}
	loadPhoto(t){
		var image = new Image();
		var canvas=this.state.coverimg

		image.src = canvas.toDataURL("image/jpeg");
		image.crossOrigin = "anonymous";
		console.log(image.src)

		var url = t.cover.url+"?type="+t.cover.params.type+"&eid="+t.cover.params.eid+"&token="+this.state.token; // File upload web service path
		var photo = {
			uri: image.src, // CameralRoll Url
			type: 'image/jpeg',
			name: 'photo.jpg',
		};

		var formData = new FormData();
		formData.append("file", photo);

		var xhr = new XMLHttpRequest();
		xhr.open('POST', url);




		xhr.onload =()=> {
			if (this.state.lgidt == 1) {
				this.loadCover(t)
			}
		};

		xhr.send(image.src);
	}
