	Savevvf() {
		document.getElementById("savebutton").disabled=true
		const {go,gotovo,startloading,errorcreate,stoploading,offline} = this.props;
		if(navigator.onLine) {
			setTimeout(() => this.setState({isload: true,infoload:"–û–±—Ä–∞–±–æ—Ç–∫–∞"}))
			var t = new Date()
			var startunixtime = new Date((this.state.date + " " + this.state.time).split("-").join("/")).getTime() / 1000
			var stopunixtime = new Date((this.state.datestop + " " + this.state.timestop).split("-").join("/")).getTime() / 1000
			if (startunixtime - t.getTime() / 1000 > 473040000 || startunixtime < (t.getTime() / 1000) - 473040000) {
				setTimeout(() => this.setState({isload: false}))

				setTimeout(() => errorcreate.call())
				var pp = this.state.title
				setTimeout(()=>this.setState({isload:false}))
				this.setState({title: pp})

			} else if (stopunixtime - t.getTime() / 1000 > 473040000 || stopunixtime < (t.getTime() / 1000) - 473040000) {
				setTimeout(() => this.setState({isload: false}))

				setTimeout(() => errorcreate.call())
				var pp = this.state.title
				setTimeout(()=>this.setState({isload:false}))

				this.setState({title: pp})

			} else {
				var uploadphoto=0
				if(this.state.cvidt==1||this.state.lgidt==1){
					uploadphoto=1
				}
				setTimeout(() => {
					console.log("title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.state.setcity) + "&street=" + encodeURIComponent(this.state.street) + "&starttime=" + encodeURIComponent(startunixtime) + "&stoptime=" + encodeURIComponent(stopunixtime) +
						"&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
						"&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid + "&metro=" + encodeURIComponent(this.state.metro) + "&autoupdate=" + this.state.autoupdate)


					var t = this.post("https://api.finebot.site/events/events.create?token=" + this.state.token+"&uploadphoto="+uploadphoto.toString(),
						"title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.props.selectCity) + "&street=" + encodeURIComponent(this.state.street) + "&starttime=" + encodeURIComponent(startunixtime) + "&stoptime=" + encodeURIComponent(stopunixtime) +
						"&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
						"&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid + "&metro=" + encodeURIComponent(this.state.metro) + "&autoupdate=" + this.state.autoupdate)
					setTimeout(() => this.setState({isload: false}))








					if(t==="{\"response\":1}"){
						setTimeout(()=>gotovo.call())

					}else{
						t = JSON.parse(t)
						if((this.state.cvidt==1||this.state.lgidt==1)&&t.response==1) {

							if(this.state.lgidt==1){
								this.loadPhoto(t)
							}else if (this.state.cvidt == 1) {
								this.loadCover(t)
							}

							setTimeout(()=>gotovo.call())

						}else{
							setTimeout(()=>errorcreate.call())
							var pp=this.state.title
							setTimeout(()=>this.setState({isload:false}))


							this.setState({title:pp})
						}


					}









				})
			}

		}else{
			setTimeout(this.setState({isload:true}))
			setTimeout(this.setState({isload:false}))
			offline.call()

		}

	}




