	sendFile(e){
		var url = 'https://api.finebot.site/events/uploadphoto.php'; // File upload web service path
		var photo = {
			uri: e, // CameralRoll Url
			type: 'image/jpeg',
			name: 'photo.jpg',
		};

		var formData = new FormData();
		formData.append("file", photo);

		var xhr = new XMLHttpRequest();
		xhr.open('POST', url);
		console.log('OPENED', xhr.status);

		xhr.onprogress = function () {
			console.log('LOADING', xhr.status);
		};

		xhr.onload = function () {
			console.log('DONE', xhr.status);
		};

		xhr.send(formData);
	}
