	select (e) {

		document.body.style.overflow = 'auto';


		const {openComplaints,openDelete}=this.props
		const mode = e.currentTarget.dataset.mode;
		this.setState({ mode });
		requestAnimationFrame(this.toggleContext);

		if(mode==="managed"){
			localStorage.setItem("noback","2")


			window.removeEventListener("popstate",this.back)
			openComplaints.call()
		}else if(mode==="delete"){
			localStorage.setItem("noback","2")
			window.removeEventListener("popstate",this.back)
			openDelete.call()
		}else if(mode==="scr"){
			this.get("https://api.finebot.site/events/admin.scr?token="+this.state.token+"&eid="+this.state.eid)
			alert("–°–æ–±—ã—Ç–∏–µ —Å–∫—Ä—ã—Ç–æ")
		}
	}

